import { Component, OnInit } from '@angular/core';
import { Animation, AnimationController, ModalController } from '@ionic/angular';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertController, LoadingController, NavController, ToastController } from '@ionic/angular';
import { AccessProviders } from '../providers/access-providers';
import { Storage } from '@ionic/storage';
import { DataService } from '../data.service';
import { NavparamService } from '../navparam.service';
import { SelectVariationComponent } from '../select-variation/select-variation.component';
import { TryPageModule } from '../try/try.module';

@Component({
  selector: 'app-view-product',
  templateUrl: './view-product.page.html',
  styleUrls: ['./view-product.page.scss'],
})
export class ViewProductPage implements OnInit {
  selectedSize: number;
  selectedColor: number;
  activeVariation: string;
  product_id: any;
  prods: any;
  prod_img: any;
  prod_name: any;
  prod_desc: any;
  ship_fee: any;
  stock: any;
  price: any;
  var_price: any="";
  image2: any;
  image3: any;
  img2=false;
  img3=false;
  datastorage: any;
  name : any;
  uid: any;
  v1=true;
  v2=true;
  vv1=false;
  vv2=false
  var1: any =[];
  var2: any =[];
  varname1: any="";
  varname2: any="";
  classi1: any='NULL';
  classi2: any='NULL';
  prod_quant: any;
  quant: any=1;
  x=false;
  z=true;

  constructor(
    private animatioCntrl: AnimationController,
    private data: DataService,
    private router: Router,
    private loadingController: LoadingController,
    private toastController: ToastController,
    private alertController: AlertController,
    private accessProviders: AccessProviders,
    public navController: NavController,
    private storage: Storage,
    private activatedRoute: ActivatedRoute,
    private navParamService: NavparamService,
    private modalController: ModalController
  ) { }

  ngOnInit() {
    this.storage.get('storage_xxx').then((res)=>{
      console.log(res);
        this.datastorage = res;
        this.name = this.datastorage.first_name;
        this.uid=this.datastorage.user_id;
    });
    this.product_id = this.navParamService.getNavData();
    console.log(this.product_id);
  }

  ionViewWillEnter(){
    this.var1=[];
    this.var2=[];
    return new Promise(resolve =>{
      let body = {
        aksi: 'get_product_details',
        product_id: this.product_id
      }
      this.accessProviders.postData(body, 'get_product_detail.php').subscribe((res:any) => {
        if(res.success == true) {
          if(res.var1!='NULL'){
            for(let datas of res.var1){
              this.var1.push(datas);
            }
            this.v1=false;
            console.log(this.var1);
          }else{
            this.v1=false;
           
          }
          if(res.var2!='NULL'){
            for(let datas of res.var2){
              this.var2.push(datas);
            }
            this.v2=false;
            console.log(this.var2);
          }else{
            this.v2=false;
            
          }

           console.log(res.var1);
           console.log(res.var2);
           console.log(res);
           console.log(res.result);
            this.prods = res.result;
            this.prod_img = this.prods.product_image;
            this.prod_name = this.prods.product_name;
            this.prod_desc = this.prods.product_desc; 
            this.stock = this.prods.stock;
            this.ship_fee = this.prods.shipping_fee;
            this.price = this.prods.price;
            this.var_price = this.prods.var_price;
            this.image2 = this.prods.image2;
            this.image3 = this.prods.image3;
            this.varname1=this.prods.varname1;
            this.varname2=this.prods.varname2;
            if(this.image2!='NULL'){
              this.img2=true;
            }if(this.image3!='NULL'){
              this.img3=true;
            }
            if( this.varname1=='NULL'&& this.varname2=='NULL'){
              this.x=true;
            }
        } else {

        }
      });    
    });
  }

  back(){
    this.navController.navigateRoot(['/home']);
  }
  showlog(a,b,c,d,e){
    console.log(a+' '+b+' '+c+' '+d+' '+e);
  }
  async addToCart(){
    if( this.varname1=='NULL'&& this.varname2=='NULL'){
     
      if(this.var_price==""){
        this.presentToast('Please select variation');
      }else if(this.quant>this.stock){
        this.presentToast('Please select valid quantity');
      }else if(this.quant<=0){
        this.presentToast('Please select valid quantity');
      }else{
        return new Promise(resolve =>{
          let body = {
            aksi: 'add_to_cart',
            product_id: this.product_id,
            uid: this.uid,
            varname1: this.varname1,
            varname2: this.varname2,
            classi1: this.classi1,
            classi2: this.classi2,
            var_price: this.var_price,
            quant: this.quant
          }
          this.accessProviders.postData(body, 'add_to_cart.php').subscribe((res:any) => {
            if(res.success == true) {
             console.log(res.msg);
             this.presentToast(res.msg);
             this.navController.navigateRoot(['/home']);
            } else {
              this.presentToast(res.msg);
            }
          });    
        });
      }
     
    }else{
    const modal = await this.modalController.create({
      component: SelectVariationComponent
    });
    await modal.present();
  }
  }
  add(){
    this.quant++;
  }
  minus(){
    this.quant--;
  }


  buyNow(){

  }

  segmentChanged(e: any) {
   /**  if(this.vv1==true){
      this.vv2=true;
      this.vv1=false
    }else if(this.vv2==true){
      this.vv2=false;
      this.vv1=true;
    }*/
    this.activeVariation = e.detail.value;

    if (this.activeVariation == 'color') {
      this.animatioCntrl.create()
      .addElement(document.querySelector('.sizes'))
      .duration(500)
      .iterations(1)
      .fromTo('transform', 'translateX(0px)', 'translateX(100%)')
      .fromTo('opacity', '1', '0.2')
      .play();

      this.animatioCntrl.create()
      .addElement(document.querySelector('.colors'))
      .duration(500)
      .iterations(1)
      .fromTo('transform', 'translateX(-100%)', 'translateX(0)')
      .fromTo('opacity', '0.2', '1')
      .play();
    } else {
      this.animatioCntrl.create()
      .addElement(document.querySelector('.sizes'))
      .duration(500)
      .iterations(1)
      .fromTo('transform', 'translateX(100%)', 'translateX(0)')
      .fromTo('opacity', '0.2', '1')
      .play();

      this.animatioCntrl.create()
      .addElement(document.querySelector('.colors'))
      .duration(500)
      .iterations(1)
      .fromTo('transform', 'translateX(0px)', 'translateX(-100%)')
      .fromTo('opacity', '1', '0.2')
      .play();
    }
  }

  changeSize(size: number) {
    this.selectedSize = size;
  }

  changeColor(color: number) {
    this.selectedColor = color;
  }
  async presentToast(a) {
    const toast = await this.toastController.create({
      message: a,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }  

}
 